body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-Grey-dark-1: #333;
  --color-white-1: #faf9f9;
  --color-list-items-1: #698474;
  --color-yellow-hover: #fe9801;
  --box-shadow-dark: 0 2rem 6rem rgba(0, 0, 0, 0.3);
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  line-height: 1.6;
  font-family: "open-sans", sans-serif;
  background-image: -webkit-gradient(
    linear,
    left top, right bottom,
    from(#333),
    to(#333));
  background-image: linear-gradient(
    to right bottom,
    #333,
    #333);
  background-image: -webkit-gradient(
    linear,
    left top, right bottom,
    from(var(--color-Grey-dark-1)),
    to(var(--color-Grey-dark-1))
  );
  background-image: linear-gradient(
    to right bottom,
    var(--color-Grey-dark-1),
    var(--color-Grey-dark-1)
  );
  background-size: cover;
  background-repeat: repeat;
}

.container {
  max-width: 50rem;
  margin: 7rem auto;
  box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  box-shadow: var(--box-shadow-dark);
  min-height: 80vh;
  background-color: #fff;
  border: 1px solid #fff3af;
  border-radius: 1rem;
  background-color: #333;
  background-color: var(--color-Grey-dark-1);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #faf9f9;
  border-bottom: 1px solid var(--color-white-1);
  height: 5rem;
}

.main-heading {
  margin-left: 2rem;
  color: #fe9801;
  color: var(--color-yellow-hover);
  font-size: 3rem;
  font-weight: 900;
  translate: all 1s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-name: bounceIn;
  animation-name: bounceIn;
}
.main-heading:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@-webkit-keyframes bounceIn {
  from,
  20%,
  40%,
  60%,
  80%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  0% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }

  20% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1);
    transform: scale3d(1.1, 1.1, 1.1);
  }

  40% {
    -webkit-transform: scale3d(0.9, 0.9, 0.9);
    transform: scale3d(0.9, 0.9, 0.9);
  }

  60% {
    opacity: 1;
    -webkit-transform: scale3d(1.03, 1.03, 1.03);
    transform: scale3d(1.03, 1.03, 1.03);
  }

  80% {
    -webkit-transform: scale3d(0.97, 0.97, 0.97);
    transform: scale3d(0.97, 0.97, 0.97);
  }

  to {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

@keyframes bounceIn {
  from,
  20%,
  40%,
  60%,
  80%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  0% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }

  20% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1);
    transform: scale3d(1.1, 1.1, 1.1);
  }

  40% {
    -webkit-transform: scale3d(0.9, 0.9, 0.9);
    transform: scale3d(0.9, 0.9, 0.9);
  }

  60% {
    opacity: 1;
    -webkit-transform: scale3d(1.03, 1.03, 1.03);
    transform: scale3d(1.03, 1.03, 1.03);
  }

  80% {
    -webkit-transform: scale3d(0.97, 0.97, 0.97);
    transform: scale3d(0.97, 0.97, 0.97);
  }

  to {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

.add-button {
  margin-right: 2rem;
  color: var(--color-white-1);
  font-size: 2rem;
  font-weight: 900;
  background-color: var(--color-yellow-hover);
  border-radius: 100px;
  border: none;
  position: relative;
  overflow: hidden;
  outline: none;
  padding: 0.5rem 1.2rem;
  cursor: pointer;
  translate: all 1s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-name: bounceIn;
  animation-name: bounceIn;
}

.add-button:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  color: var(--color-Grey-dark-1);
}

.add-button:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}
.button-small {
  font-size: 0.9rem;
  font-weight: 900;
  margin-right: 0.3rem;
  border-radius: 100%;
  background-color: var(--color-white-1);
  color: #c72c41;
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
  -webkit-animation-duration: 0.7s;
  animation-duration: 0.7s;
  display: inline-block;
}
.button-small:hover {
  background-color: #c72c41;
  color: var(--color-white-1);
}

.input {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2rem auto;
}

.input-field {
  flex-grow: 1;
  font-family: inherit;
  font-size: 2rem;
  font-weight: 600;
  background-color: #333;
  border: none;
  letter-spacing: 0.2rem;
  color: var(--color-white-1);
  border-bottom: 1px solid var(--color-list-items-1);
  padding: 1rem 1rem;
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-transition: all 3s;
  transition: all 3s;
}
.input-field:focus {
  outline: transparent;
}

.input-field::-webkit-input-placeholder {
  font-weight: 900;
  font-size: 1.2rem;
  text-align: center;
  color: var(--color-yellow-hover);
}

.input-field::-moz-placeholder {
  font-weight: 900;
  font-size: 1.2rem;
  text-align: center;
  color: var(--color-yellow-hover);
}

.input-field:-ms-input-placeholder {
  font-weight: 900;
  font-size: 1.2rem;
  text-align: center;
  color: var(--color-yellow-hover);
}

.input-field::-ms-input-placeholder {
  font-weight: 900;
  font-size: 1.2rem;
  text-align: center;
  color: var(--color-yellow-hover);
}

.input-field::placeholder {
  font-weight: 900;
  font-size: 1.2rem;
  text-align: center;
  color: var(--color-yellow-hover);
}

.saved-todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  /* border-bottom: 1px solid var(--color-list-items-1); */
  font-size: 2rem;
  font-weight: 600;
  margin: 1rem auto;
  list-style: none;
  color: var(--color-white-1);
  text-transform: capitalize;
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
  -webkit-animation-duration: 0.4s;
  animation-duration: 0.4s;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.saved-todo__list {
  list-style-type: none;
  text-align: left;
  margin: 2rem;
}
input[type="radio"] {
  top: 0;
  left: 0;
  margin-left: 0.5rem;
  height: 1.2rem;
  width: 1.2rem;
  cursor: pointer;
  -webkit-appearance: button;
  border: double 2px var(--color-white-1);
  background-color: #c72c41;
  outline: none;
  overflow: hidden;
  border-radius: 15px;
}

input[type="radio"]:checked {
  background-color: #2c786c;
  /* color: #333; */
}

input[type="radio"]:hover {
  box-shadow: 0px 0px 10px #2c786c;
}
.todo-main__items {
  display: inline-block;
  position: absolute;
  margin-left: 9rem;
}

@-webkit-keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}

@keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}

